import React from 'react';
import { DashboardStats } from '../../types/dashboard';
import { Calendar, FileText, CheckCircle, TrendingUp } from 'lucide-react';

interface StatsOverviewProps {
  stats: DashboardStats;
}

export const StatsOverview: React.FC<StatsOverviewProps> = ({ stats }) => {
  const statCards = [
    {
      icon: <Calendar className="w-8 h-8 text-blue-600" />,
      title: 'Today\'s Appointments',
      value: stats.appointments_today,
      color: 'bg-blue-50 border-blue-200',
      textColor: 'text-blue-900'
    },
    {
      icon: <FileText className="w-8 h-8 text-orange-600" />,
      title: 'Pending Transcripts',
      value: stats.pending_transcripts,
      color: 'bg-orange-50 border-orange-200',
      textColor: 'text-orange-900'
    },
    {
      icon: <CheckCircle className="w-8 h-8 text-green-600" />,
      title: 'Completed Visits',
      value: stats.completed_visits,
      color: 'bg-green-50 border-green-200',
      textColor: 'text-green-900'
    },
    {
      icon: <TrendingUp className="w-8 h-8 text-purple-600" />,
      title: 'Completion Rate',
      value: stats.appointments_today > 0 
        ? `${Math.round((stats.completed_visits / stats.appointments_today) * 100)}%`
        : '0%',
      color: 'bg-purple-50 border-purple-200',
      textColor: 'text-purple-900'
    }
  ];

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
      {statCards.map((stat, index) => (
        <div
          key={index}
          className={`${stat.color} border rounded-xl p-6 transition-all duration-200 hover:shadow-md`}
        >
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-600 mb-1">{stat.title}</p>
              <p className={`text-3xl font-bold ${stat.textColor}`}>
                {stat.value}
              </p>
            </div>
            <div className="flex-shrink-0">
              {stat.icon}
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};
