import React from 'react';
import { Link } from 'react-router-dom';
import { MedicalRecord } from '../../types/medicalRecord';
import { Activity, RefreshCw, FileText, Calendar } from 'lucide-react';

interface RecentActivityCardProps {
  medicalRecords: MedicalRecord[];
  onRefresh: () => void;
}

export const RecentActivityCard: React.FC<RecentActivityCardProps> = ({
  medicalRecords,
  onRefresh
}) => {
  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      month: 'short',
      day: 'numeric',
      hour: 'numeric',
      minute: '2-digit'
    });
  };

  const getRecordTypeColor = (recordType: string) => {
    switch (recordType.toLowerCase()) {
      case 'checkup':
        return 'bg-green-100 text-green-800';
      case 'emergency':
        return 'bg-red-100 text-red-800';
      case 'surgery':
        return 'bg-purple-100 text-purple-800';
      case 'vaccination':
        return 'bg-blue-100 text-blue-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  return (
    <div className="bg-white rounded-xl shadow-sm border border-gray-200">
      {/* Card Header */}
      <div className="px-6 py-4 border-b border-gray-200">
        <div className="flex items-center justify-between">
          <div className="flex items-center">
            <Activity className="w-5 h-5 text-green-600 mr-2" />
            <h2 className="text-xl font-semibold text-gray-900">Recent Activity</h2>
          </div>
          <div className="flex items-center space-x-4">
            <div className="text-sm text-gray-600">
              Last {medicalRecords.length} records
            </div>
            <button
              onClick={onRefresh}
              className="p-2 text-gray-400 hover:text-gray-600 transition-colors"
            >
              <RefreshCw className="w-4 h-4" />
            </button>
          </div>
        </div>
      </div>

      {/* Content */}
      <div className="px-6 py-4">
        {medicalRecords.length === 0 ? (
          <div className="text-center py-8">
            <FileText className="w-12 h-12 text-gray-300 mx-auto mb-3" />
            <p className="text-gray-500">No recent medical records</p>
          </div>
        ) : (
          <div className="space-y-4">
            {medicalRecords.map((record) => (
              <div
                key={record.id}
                className="p-4 rounded-lg border border-gray-200 bg-gray-50 hover:bg-gray-100 transition-colors"
              >
                <div className="flex items-start justify-between">
                  <div className="flex-1">
                    <div className="flex items-center mb-2">
                      <span className={`px-2 py-1 rounded-full text-xs font-medium ${getRecordTypeColor(record.record_type)}`}>
                        {record.record_type}
                      </span>
                      {record.follow_up_required && (
                        <span className="ml-2 px-2 py-1 bg-orange-100 text-orange-800 rounded-full text-xs font-medium">
                          Follow-up Required
                        </span>
                      )}
                    </div>
                    
                    <h4 className="font-medium text-gray-900 mb-1">{record.title}</h4>
                    
                    {record.description && (
                      <p className="text-sm text-gray-600 mb-2 line-clamp-2">
                        {record.description}
                      </p>
                    )}
                    
                    <div className="flex items-center text-sm text-gray-500 space-x-4">
                      <div className="flex items-center">
                        <Calendar className="w-4 h-4 mr-1" />
                        <span>{formatDate(record.visit_date)}</span>
                      </div>
                      {record.veterinarian_name && (
                        <span>Dr. {record.veterinarian_name}</span>
                      )}
                    </div>
                  </div>
                  
                  <div className="flex items-center space-x-2">
                    <Link
                      to={`/pets/${record.pet_id}`}
                      className="text-blue-600 hover:text-blue-800 text-sm font-medium"
                    >
                      View Pet
                    </Link>
                    <Link
                      to={`/pets/${record.pet_id}/medical-records/${record.id}`}
                      className="bg-blue-600 text-white px-3 py-1 rounded-md text-sm hover:bg-blue-700 transition-colors"
                    >
                      View Record
                    </Link>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};
